h2 {
  font-size: 26px;
}

h3 {
  font-size: 20px;
}

.invisible {
  visibility: hidden;
}

.cat-info {
  flex-direction: row;
  justify-content: left;
  gap: 20px;
  display: flex;
}

.cat-info p {
  width: 600px;
  font-size: 20px;
}

.loader {
  perspective: 1000px;
  width: 108px;
  height: 108px;
  color: #fff;
  border-radius: 50%;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader:after {
  color: #ff3d00;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
}

select {
  font: inherit;
  box-sizing: border-box;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background-color: #c3ebc3;
  border: thin solid #41f730;
  border-radius: 4px;
  padding: .5em 3.5em .5em 1em;
  line-height: 1.5em;
  display: inline-block;
}

select.breed-select {
  background-image: linear-gradient(45deg, rgba(0, 0, 0, 0) 50%, #044802 50%), linear-gradient(135deg, #044802 50%, rgba(0, 0, 0, 0) 50%), linear-gradient(to right, #037f18, #41f730);
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), 100% 0;
  background-repeat: no-repeat;
  background-size: 5px 5px, 5px 5px, 2.5em 2.5em;
}

